@import "../../../assets/scss/mediaqueries";

.cell {
  width: 100%;
  text-align: center;
}

@include breakpoints( "small" ) {
  .cell {
    font-size: 10px;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
  }
}
